* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: sans-serif;
}

header {
  min-height: 10vh;
  background-color: #1895b1;
  border-bottom: 1px solid #117e97;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.is-hidden {
  visibility: hidden;
}

.search-form {
  padding: 2rem;
  display: flex;
}

.search-form input {
  width: 100%;
  border: 3px solid #117e97;
  border-radius: 10px 0 0 10px;
  padding: .5rem;
  font-size: 1rem;
}

.search-form input:focus {
  outline: none;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background: #117e97;
  border: none;
  border-radius: 0 10px 10px 0;
  padding: 1rem;
  font-size: 1rem;
}

.info {
  color: gray;
  justify-content: space-between;
  align-items: center;
  padding: .5rem 0;
  font-size: 14px;
  display: flex;
}

.info a {
  color: gray;
}

.gallery {
  width: 100%;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 4rem 2rem;
  margin: auto;
  padding: 2rem;
  display: grid;
}

.gallery img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.load-more {
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  height: 40px;
  text-align: center;
  background-color: #117e97;
  border: 2px solid #117e97;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  margin: 20px auto;
  padding: 0 17px;
  font-size: 16px;
  font-weight: 600;
  text-decoration: none;
  display: flex;
}

.load-more:hover {
  fill: #a3c7d6;
  background-color: #117e97;
  border-color: #117e97;
}

#myBtn {
  z-index: 99;
  cursor: pointer;
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 5px;
  outline: none;
  padding: 10px 10px 5px;
  font-size: 30px;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
}

#myBtn:hover {
  background-color: #a3c7d6;
}

body.hidden-scroll {
  overflow: hidden;
}

.sl-overlay {
  z-index: 1035;
  background: #fff;
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.sl-wrapper {
  z-index: 1040;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.sl-wrapper * {
  box-sizing: border-box;
}

.sl-wrapper button {
  cursor: pointer;
  background: none;
  border: 0;
  padding: 0;
  font-size: 28px;
}

.sl-wrapper button:hover {
  opacity: .7;
}

.sl-wrapper .sl-close {
  z-index: 10060;
  height: 44px;
  width: 44px;
  color: #000;
  margin-top: -14px;
  margin-right: -14px;
  font-family: Arial, Baskerville, monospace;
  font-size: 3rem;
  line-height: 44px;
  display: none;
  position: fixed;
  top: 30px;
  right: 30px;
}

.sl-wrapper .sl-counter {
  z-index: 1060;
  color: #000;
  font-size: 1rem;
  display: none;
  position: fixed;
  top: 30px;
  left: 30px;
}

.sl-wrapper .sl-navigation {
  width: 100%;
  display: none;
}

.sl-wrapper .sl-navigation button {
  height: 44px;
  width: 22px;
  text-align: center;
  z-index: 10060;
  color: #000;
  margin-top: -22px;
  font-family: Arial, Baskerville, monospace;
  line-height: 44px;
  display: block;
  position: fixed;
  top: 50%;
}

.sl-wrapper .sl-navigation button.sl-next {
  font-size: 2rem;
  right: 5px;
}

.sl-wrapper .sl-navigation button.sl-prev {
  font-size: 2rem;
  left: 5px;
}

@media (min-width: 35.5em) {
  .sl-wrapper .sl-navigation button {
    width: 44px;
  }

  .sl-wrapper .sl-navigation button.sl-next {
    font-size: 3rem;
    right: 10px;
  }

  .sl-wrapper .sl-navigation button.sl-prev {
    font-size: 3rem;
    left: 10px;
  }
}

@media (min-width: 50em) {
  .sl-wrapper .sl-navigation button {
    width: 44px;
  }

  .sl-wrapper .sl-navigation button.sl-next {
    font-size: 3rem;
    right: 20px;
  }

  .sl-wrapper .sl-navigation button.sl-prev {
    font-size: 3rem;
    left: 20px;
  }
}

.sl-wrapper.sl-dir-rtl .sl-navigation {
  direction: ltr;
}

.sl-wrapper .sl-image {
  -ms-touch-action: none;
  touch-action: none;
  z-index: 10000;
  position: fixed;
}

.sl-wrapper .sl-image img {
  width: 100%;
  height: auto;
  border: 0;
  margin: 0;
  padding: 0;
  display: block;
}

@media (min-width: 35.5em) {
  .sl-wrapper .sl-image img {
    border: 0;
  }
}

@media (min-width: 50em) {
  .sl-wrapper .sl-image img {
    border: 0;
  }
}

.sl-wrapper .sl-image iframe {
  background: #000;
  border: 0;
}

@media (min-width: 35.5em) {
  .sl-wrapper .sl-image iframe {
    border: 0;
  }
}

@media (min-width: 50em) {
  .sl-wrapper .sl-image iframe {
    border: 0;
  }
}

.sl-wrapper .sl-image .sl-caption {
  color: #fff;
  background: rgba(0, 0, 0, .8);
  padding: 10px;
  font-size: 1rem;
  display: none;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.sl-wrapper .sl-image .sl-caption.pos-top {
  top: 0;
  bottom: auto;
}

.sl-wrapper .sl-image .sl-caption.pos-outside {
  bottom: auto;
}

.sl-wrapper .sl-image .sl-download {
  color: #000;
  z-index: 1060;
  display: none;
  position: absolute;
  bottom: 5px;
  right: 5px;
}

.sl-spinner {
  height: 40px;
  opacity: 0;
  width: 40px;
  z-index: 1007;
  -ms-animation: pulsate 1s ease-out infinite;
  border: 5px solid #333;
  border-radius: 40px;
  margin: -20px 0 0 -20px;
  animation: pulsate 1s ease-out infinite;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
}

.sl-scrollbar-measure {
  width: 50px;
  height: 50px;
  position: absolute;
  top: -9999px;
  overflow: scroll;
}

.sl-transition {
  transition: transform .2s;
}

@keyframes pulsate {
  0% {
    opacity: 0;
    transform: scale(.1);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: scale(1.2);
  }
}

